@charset 'utf-8';

.bannerWrap { 
  width: 1300px;
  height: 450px;
  margin: auto;
  background-color: rgb(245, 235, 220);
  color: #333;
  overflow: hidden;
}
.flexbox { display: flex; }
.column { flex-direction: column; }
.alignItem { align-items: center; }
.wid100 { width: 1300px; }
.img { 
  width: 400px; 
  height: 400px; 
  object-fit: cover; 
  flex: none;
  transform: translateX();
}
.txtBx { width: 900px; padding: 0 100px; }
.banner { transition: 0.5s;}
.btBox { display: flex; justify-content: center;}